export function clickMap(feature) {
  return {
    type: 'MAP_CLICK',
    payload: feature
  }
}

export function changeZone(selection) {
  return {
    type: 'ZONE_SELECT',
    payload: selection
  }
}

export function changePd(selection) {
  console.log('changePd action dispatching');
  return {
    type: 'OVERLAY_SELECT',
    payload: selection
  }
}

export function changeMaxVal(event) {
  return {
    type: 'MAX_VAL',
    payload: event.target.value
  }
}

export function changeMinVal(event) {
  return {
    type: 'MIN_VAL',
    payload: event.target.value
  }
}

export function loadSource(event) {
  return {
    type: 'LOAD_SOURCE',
    payload: event
  }
}

export function showFAR(event) {
  return {
    type: 'SHOW_FAR',
    payload: event.target.value
  }
}

export function showZoneColors(event) {
  return {
    type: 'SHOW_ZONE_COLORS',
    payload: event.target.value
  }
}

export function changeUse(event) {
  console.log(event.target.value);
  return {
    type: 'CHANGE_USE',
    payload: event.target.value
  }
}



