import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Immutable from 'immutable';
import Select from 'react-select-plus';

import { zoneOptions, presets, pdOptions } from '../data/zones';
import { changeZone, changePd, changeMaxVal, changeMinVal, showFAR, showZoneColors, changeUse } from '../actions/index';

class Interface extends Component {
  constructor(props){
    super(props);
    this.state = {
      // loaded: false,
      presets: {
        any: true,
        residential: false,
        office: false,
        retail: false
      },
      height: {
        far: true,
        avail: false
      },
      colorBy: {
        zone: true
      }
    }

    this._presetUses = this._presetUses.bind(this);
    this._intersectArrays = this._intersectArrays.bind(this);
  }

  _heightViz(event) {
    if(event.target.value === 'far') {
      this.setState({ height: { far: true, avail: false }});
    } else {
      this.setState({ height: { far: false, avail: true }});
    }
    this.props.showFAR(event);
  }

  _colorViz(event){
    if(event.target.value === 'zone') {
      this.setState({ colorBy: { zone: true }});
    } else {
      this.setState({ colorBy: { zone: false }});
    }
    this.props.showZoneColors(event);
  }

  _presetUses(event) {
    // TODO: This is a really fucked up way to do this whole function...
    if(event.target.value === 'any') {
      this.setState({ presets: { any: true, residential: false, office: false, retail: false } });
      this.props.changeZone(presets.any);
    } else {
      const newState = this.state.presets;
      newState[event.target.value] = !newState[event.target.value];
      newState.any = false;

      // If all have been unchecked, reset any to true
      if(Object.values(newState).reduce((p,c) => p+c) === 0) {
        this.setState({ presets: { any: true, residential: false, office: false, retail: false } });
        this.props.changeZone(presets.any);
      } else {
        // Perform a logical intersect of presets (return zones appearing in all presets)
        const activeZones = Object.keys(newState)
          .filter(key => newState[key] === true)
          .map(key => presets[key].map(obj => obj.value))
          .reduce((p,c) => this._intersectArrays(p,c))
          .map(zone => {return { label: zone, value: zone }});

        this.setState({ presets: newState })
        this.props.changeZone(activeZones);
      }
    }
  }

  _intersectArrays(a, b) {
    let t;
    if(b.length > a.length) t = b, b = a, a = t; //indexOf to loop over shorter
    return a.filter(e => b.indexOf(e) > -1)
            .filter((e,i,c) => c.indexOf(e) === i);
  }

  render() {
    return (
      <div id='ui' style={ STYLE.ui }>
        <div className='filters' id='filter-div'>
          <label>Filters:</label><br />
          <div id='use-filter' style={{ marginBottom: '20px' }}>
            <span>Allowed Use Presets</span><br />
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='any'
                checked={ this.state.presets.any }
                onChange={ this._presetUses.bind(this) } /> Any
            </div>
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='residential'
                checked={ this.state.presets.residential }
                onChange={ this._presetUses.bind(this) } /> Residential
            </div>
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='office'
                checked={ this.state.presets.office }
                onChange={ this._presetUses.bind(this) } /> Office
            </div>
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='retail'
                checked={ this.state.presets.retail }
                onChange={ this._presetUses } /> Retail
            </div>
          </div>
          <br />
          <div id='zone-filter'>
            <span>Select Zone</span>
            <Select
              multi
              name="form-field-name"
              value={ this.props.select_val }
              options={ zoneOptions }
              onChange={ this.props.changeZone } />
          </div>
          <br />
          <div id='size-filter'>
            <span>Select Size</span><br />
            <input
              type='text'
              name='sizeMin'
              value={ this.props.minValue }
              style={{ width: '49%' }}
              onChange={ this.props.changeMinVal } />
            <input
              type='text'
              name='sizeMax'
              value={ this.props.maxValue }
              style={{ width: '49%' }}
              onChange={ this.props.changeMaxVal } />
          </div>
        </div>

        <br /><br />
        <div className='overlay' id='overlay-div'>
          <label>Overlays: </label><br />
          <div id='overlay-PD'>
            <span>Plan Districts</span>
            <Select
              multi
              name="PDSelect"
              value={ this.props.overlay_pd }
              options={ pdOptions }
              onChange={ this.props.changePd } />
          </div>
        </div>

        <br /><br />
        <div className='viz' id='viz-div'>
          <label>Visualization: </label><br />
          <div id='height-source'>
            <span>Height</span><br />
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='far'
                checked={ this.state.height.far }
                onChange= { this._heightViz.bind(this) } /> FAR
            </div>
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='avail'
                checked={ this.state.height.avail }
                onChange= { this._heightViz.bind(this) } /> Available FAR
            </div>
          </div>
          <br /><br />
          {/* <span>Height</span>
          <input
            type='checkbox'
            name='far'
            style={{ marginLeft: '10px' }}
            onChange={ this.props.showFAR }/> */}
          <div id='color-source'>
            <span>Color by</span><br />
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='zone'
                checked={ this.state.colorBy.zone }
                onChange={ this._colorViz.bind(this) }/> Zone
            </div>
            <div style={{ float:'left', marginLeft: '10px' }}>
              <input
                type='checkbox'
                value='none'
                checked={ !this.state.colorBy.zone }
                onChange={ this._colorViz.bind(this) }/> None
            </div>
          </div>
        </div>
      </div>
    )
  }
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    changeZone: changeZone,
    changePd: changePd,
    changeMaxVal: changeMaxVal,
    changeMinVal: changeMinVal,
    showFAR: showFAR,
    showZoneColors: showZoneColors,
    changeUse: changeUse
  }, dispatch);
}
function mapStateToProps(state) {
  return {
    select_val: state.userInterface.get('select_val'),
    overlay_pd: state.userInterface.get('overlay_pd'),
    maxValue: state.userInterface.get('max_val'),
    minValue: state.userInterface.get('min_val'),
    use: state.userInterface.get('use')
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Interface);

const STYLE = {
  ui: {
    zIndex: 2,
    position: 'absolute',
    top: '0px',
    bottom: '0px',
    right: '0px',
    borderRadius: '7px',
    width: '400px',
    background: 'white',
    padding: '15px'
  }
};



